.reviews {
  @include section();
  background-color: $grey3;
  &__title {
    @include h2();
  }
  &__items {
    display: flex;
    flex-direction: column;
    @media ($tablet) {
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }
  .review {
    margin-bottom: 5rem;
    &:last-child {
      margin-bottom: 0;
    }
    @media ($tablet) {
      margin-bottom: 0;
      flex-basis: calc(50% - 1.6rem);
      &:nth-child(n + 3) {
        margin-top: 2.4rem;
      }
    }
  }
}
