.stage {
  @include section();
  background-color: $primaryColor;
  &__wrapper {
    display: flex;
    flex-direction: column-reverse;
    @media ($tablet) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  &__title {
    @include h1();
  }
  &__list {
    @include lead();
    &-item {
      list-style: url("../img/check_icon.png");
      padding-left: 0.5rem;
    }
  }
  &__img {
    @include img($white);
    width: 100%;
    height: 214px;
    margin-bottom: 2rem;
    @media ($tablet) {
      flex-shrink: 0;
      max-width: 300px;
      margin-left: 4rem;
      margin-bottom: 0;
    }
    @media ($desktop) {
      max-width: 469px;
      margin-left: 8rem;
    }
  }
}
