.about {
  @include section();
  background-color: $white;
  &__wrapper {
    display: flex;
    flex-direction: column-reverse;
    @media ($tablet) {
      flex-direction: row;
    }
  }
  &__title {
    @include h2($align: left);
  }
  &__desc {
    @include p1();
    margin-bottom: 2rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__img {
    @include img();
    width: 100%;
    height: 245px;
    margin-bottom: 1.5rem;
    @media ($tablet) {
      width: 250px;
      flex-shrink: 0;
      margin-bottom: 0;
      margin-left: 5rem;
    }
    @media ($desktop) {
      width: 430px;
      margin-left: 9.7rem;
    }
  }
}
